import React from "react"
//import { Link } from "gatsby"

import Layout from "../components/layout"
import Experience from "../components/experience"
import SEO from "../components/seo"
import ITSkills from "../components/itSkills"

const IndexPage = () => (

  <Layout>
    <SEO title="Home" />

    <h2 style={{ padding: `3.2rem 1.0875rem 0rem` }}>Hello, welcome to my website</h2>
    <p >I am Yuxin Tang, a full stack developer.</p>
    <p> Passionate about cutting-edge technologies and software engineering.</p>
    {/* <p> Open to new opportunity as frontend, backend and full stack developer.</p> */}
    <br></br>
    <div style={{
      width: '100%',
      height: '100%',
      background: '#ebf0f7',
      color: 'black',
      padding: '0 0 1rem 0',
      maxWidth: '1000px',
      marginLeft: 'auto',
      marginRight: 'auto',
    }}>

      <ITSkills />
    </div>
    {/* <Link to="/page-2">to page 2</Link> */}
    <Experience />
    <footer>
      <div style={{ height: '100px' }}></div>
    </footer>
  </Layout>

)

export default IndexPage
